    get gridOptions() {
        if (this.useFavorite && this.columns && this.columns.length > 0 && !this.columns.find(item => item.field === FAVORITE_FIELD_NAME)) {
            this.columns.push({
                field: FAVORITE_FIELD_NAME, title: '', width: 40, align: 'center',
                formatter: this.favHelper.favoriteColumnFormatter
            });
        }
        this._gridOptions = Object.assign(this._gridOptions, {
            singleSelect: this.singleSelect,
            idField: this.idField,
            uri: this.uri,
            showFilterBar: this.showFilterBar,
            pagination: this.pagination,
            pageIndex: this.pageIndex,
            pageSize: this.pageSize,
            pageList: this.pageList,
            total: this.total,
            items: this.items,
            columns: this.columns,
            resizableColumns: true,
            fixedHeader: true,
            hover: true,
            treeInfo: this.treeInfo
        });

        return this._gridOptions;
    }
